*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}



body {
  background-color: #fff;
  font-family: 'Open Sans', sans-serif ;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a,h1,p{
  color: #333;
  text-decoration: none;
  margin: 0 10px;
}

a:hover{
  transition: all .4s ease-in-out;
}

